.exp-container {
    width: 100%;
    background-color: #34495E;
    display: flex;
    justify-content: center;
    padding-top: 5%;
    padding-bottom: 5%;

}

.map {
    width: 50%;
    position: relative;
}

.peoples-gas {
    height: 5%;
    width: 1.5%;
    background-color: #34495E;
    border-radius: 50%;
    position: absolute;
    margin-top: 17%;
    left: 30%;
}

.peoples-gas:hover {
    height: 6%;
    width: 1.75%;
    background-color: #34495E;
    border-radius: 50%;
    position: absolute;
    margin-top: 17%;
    left: 30%;
}

