.header {
  text-align: center;
  position: relative;
  width: 100%;
  padding-top: 4%;
}

.header-image {
  opacity: 0.5;
  display: block;
  width: 100%;
  backface-visibility: hidden;
}

.header-middle {
  transition: .5s ease;
  opacity: 0.5;
  position: absolute;
  top: 60%;
  left: 55%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.header-middle h1 {
  color: black;
  font-size: 400%;
  font-family: "Malgun Gothic";
  margin-bottom: 5%;
}

.header-middle h2 {
  color: black;
  font-size: 300%;
  font-family: "Malgun Gothic";
  border-top: 2px solid black;
  border-bottom: 2px solid black;
}