.project-container {
}

.project {
    display: flex;
    justify-content: center;
    margin: 1%;
    padding-top: 5%;
}

.project-content {
    display: flex;
    flex-direction: column;
    margin: 1%;
    padding: 1%;
    width: 50%;
    box-shadow: 5px 10px 18px #888888;
}

.project-name {
    font-family: "Malgun Gothic";
    margin-bottom: 0;
}

.project-date {
    font-family: "Malgun Gothic";
    font-size: 100%;
    margin: 0;
}

.project-description {
    font-family: "Malgun Gothic";
}

.project-image {
    width: 30%;
    box-shadow: 5px 10px 18px #888888;
}