
.nav-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    width: 100%;
    background-color: #34495E;
}

.nav-container a {
    height: 50px;
    line-height: 50px;
    color: white;
}

.nav-container-ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: #34495E;
    margin: 0.5%;
}

.nav-container-ul a {
    text-decoration: none;
    padding-left: 5%;
    padding-right: 5%;
}

@media only screen and (max-width: 800px) {

    .nav-container-ul {
        display: none;
        width: 100%;
        margin-top: 50px;
        background-color: #008378;
    }

    .nav-container-ul {
        display: inline-block;
    }
}